.stars-line {
  display: flex;

  @for $i from 0 through 5 {
    &--#{$i} &__star:nth-child(-n + #{$i}) {
      background-image: url(../images/star-active.svg);
    }
  }

  &__star {
    width: 16px;
    height: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;
  }
}
